import { useReducer } from "react";
import {
  UPDATE_POKES,
  ADD_TO_CART,
  UPDATE_CART_QUANTITY,
  REMOVE_FROM_CART,
  ADD_MULTIPLE_TO_CART,
  UPDATE_SHINIES,
  UPDATE_CURRENT_SHINY,
  CLEAR_CART,
  TOGGLE_CART
} from "./actions";

export const reducer = (state, action) => {
  switch (action.type) {
    case UPDATE_POKES:
      return {
        ...state,
        pokes: [...action.pokes],
      };

    case ADD_TO_CART:
      return {
        ...state,
        cartOpen: true,
        cart: [...state.cart, action.poke],
      };

    case ADD_MULTIPLE_TO_CART:
      return {
        ...state,
        cart: [...state.cart, ...action.pokes],
      };

    case UPDATE_CART_QUANTITY:
      return {
        ...state,
        cartOpen: true,
        cart: state.cart.map(poke => {
          if (action._id === poke._id) {
            poke.purchaseQuantity = action.purchaseQuantity
          }
          return poke
        })
      };

    case REMOVE_FROM_CART:
      let newState = state.cart.filter(poke => {
        return poke._id !== action._id;
      });

      return {
        ...state,
        cartOpen: newState.length > 0,
        cart: newState
      };

    case CLEAR_CART:
      return {
        ...state,
        cartOpen: false,
        cart: []
      };

    case TOGGLE_CART:
      return {
        ...state,
        cartOpen: !state.cartOpen
      };

    case UPDATE_SHINIES:
      return {
        ...state,
        shinies: [...action.shinies],
      };

    case UPDATE_CURRENT_SHINY:
      return {
        ...state,
        currentShiny: action.currentShiny
      }

    default:
      return state;
  }
};

export function usePokeReducer(initialState) {
  return useReducer(reducer, initialState)
}
