import { gql } from '@apollo/client';

export const QUERY_POKES = gql`
  query getPokes($shiny: ID) {
    pokes(shiny: $shiny) {
      _id
      name
      description
      price
      quantity
      image
      shiny {
        _id
      }
    }
  }
`;

export const QUERY_CHECKOUT = gql`
  query getCheckout($pokes: [ID]!) {
    checkout(pokes: $pokes) {
      session
    }
  }
`;

export const QUERY_ALL_POKES = gql`
  {
    pokes {
      _id
      name
      description
      price
      quantity
      shiny {
        name
      }
    }
  }
`;

export const QUERY_SHINIES = gql`
  {
    shinies {
      _id
      name
    }
  }
`;

export const QUERY_USER = gql`
  {
    user {
      firstName
      lastName
      orders {
        _id
        purchaseDate
        pokes {
          _id
          name
          description
          price
          quantity
          image
        }
      }
    }
  }
`;
