import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useQuery } from '@apollo/client';

import Cart from '../components/Cart';
import { useStoreContext } from '../utils/GlobalState';
import {
  REMOVE_FROM_CART,
  UPDATE_CART_QUANTITY,
  ADD_TO_CART,
  UPDATE_POKES,
} from '../utils/actions';
import { QUERY_POKES } from '../utils/queries';
import { idbPromise } from '../utils/helpers';
import spinner from '../assets/spinner.gif';

function Detail() {
  const [state, dispatch] = useStoreContext();
  const { id } = useParams();

  const [currentPoke, setCurrentPoke] = useState({});

  const { loading, data } = useQuery(QUERY_POKES);

  const { pokes, cart } = state;

  useEffect(() => {
    // already in global store
    if (pokes.length) {
      setCurrentPoke(pokes.find((poke) => poke._id === id));
    }
    // retrieved from server
    else if (data) {
      dispatch({
        type: UPDATE_POKES,
        pokes: data.pokes,
      });

      data.pokes.forEach((poke) => {
        idbPromise('pokes', 'put', poke);
      });
    }
    // get cache from idb
    else if (!loading) {
      idbPromise('pokes', 'get').then((indexedPokes) => {
        dispatch({
          type: UPDATE_POKES,
          pokes: indexedPokes,
        });
      });
    }
  }, [pokes, data, loading, dispatch, id]);

  const addToCart = () => {
    const itemInCart = cart.find((cartItem) => cartItem._id === id);
    if (itemInCart) {
      dispatch({
        type: UPDATE_CART_QUANTITY,
        _id: id,
        purchaseQuantity: parseInt(itemInCart.purchaseQuantity) + 1,
      });
      idbPromise('cart', 'put', {
        ...itemInCart,
        purchaseQuantity: parseInt(itemInCart.purchaseQuantity) + 1,
      });
    } else {
      dispatch({
        type: ADD_TO_CART,
        poke: { ...currentPoke, purchaseQuantity: 1 },
      });
      idbPromise('cart', 'put', { ...currentPoke, purchaseQuantity: 1 });
    }
  };

  const removeFromCart = () => {
    dispatch({
      type: REMOVE_FROM_CART,
      _id: currentPoke._id,
    });

    idbPromise('cart', 'delete', { ...currentPoke });
  };

  return (
    <>
      {currentPoke && cart ? (
        <div className="container my-1">
          <Link to="/">‚Üê Back to Pokes</Link>

          <h2>{currentPoke.name}</h2>

          <p>{currentPoke.description}</p>

          <p>
            <strong>Price:</strong>${currentPoke.price}{' '}
            <button onClick={addToCart}>Add to Cart</button>
            <button
              disabled={!cart.find((p) => p._id === currentPoke._id)}
              onClick={removeFromCart}
            >
              Remove from Cart
            </button>
          </p>

          <img
            src={`/images/${currentPoke.image}`}
            alt={currentPoke.name}
          />
        </div>
      ) : null}
      {loading ? <img src={spinner} alt="loading" /> : null}
      <Cart />
    </>
  );
}

export default Detail;
